<template>
  <transition name="slide">
    <div class="add-song" v-show="isShow" @click.stop>
      <!--标题-->
      <div class="header">
        <h1 class="title">添加歌曲到列表</h1>
      </div>
      <!--搜索框-->
      <div class="search-box-wrapper">
        <search-box ref="searchBox" :placeholder="'搜索歌曲'" @query="onQueryChange"></search-box>
      </div>
      <!--快速入口-->
      <div class="shortcut" v-show="!query">
        <!--切换开关-->
        <switches :switches="switchTitle"
                  :currentIndex="currentPage"
                  @switchToggle="toggle"
        >
        </switches>
        <div class="list-wrapper">
          <!--最近播放-->
          <scroll ref="songList"
                  class="list-scroll"
                  v-if="currentPage===0"
                  :data="playHistory">
            <div class="list-inner">
              <song-list :songs="playHistory" @select="addSong">
              </song-list>
            </div>
          </scroll>
          <!--搜索历史-->
          <scroll ref="searchList"
                  class="list-scroll"
                  v-if="currentPage===1"
                  :data="searchHistory"
                  :refreshDelay="100">
            <div class="list-inner">
              <search-list :searches="searchHistory"
                           @select="addQuery"
                           @delete="deleteItem">
              </search-list>
            </div>
          </scroll>
        </div>
      </div>
      <!--搜索结果-->
      <div class="search-result" v-show="query">
        <suggest :query="query"
                 :isSearchSinger="isShowSinger"
                 @select="saveHistory"
                 @listScroll="blurInput"></suggest>
      </div>
      <!--顶部提示-->
      <top-tip ref="topTip" :delay="3000">
        <div class="tip-title">
          <i class="icon-ok"></i>
          <span class="text">1首歌曲已经添加到播放列表</span>
        </div>
      </top-tip>
      <!--关闭按钮-->
      <div class="close" @click="hide">
        关闭
      </div>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  import SearchBox from '../../base/search-box/search-box'
  import SongList from '../../base/song-list/song-list'
  import SearchList from '../../base/search-list/search-list'
  import Scroll from '../../base/scroll/scroll'
  import Switches from '../../base/switches/switches'
  import TopTip from '../../base/top-tip/top-tip'
  import Suggest from '../../components/suggest/suggest'
  import {mapGetters, mapActions, mapMutations} from 'vuex'
  import Song from '../../common/js/song'

  export default {
    data() {
      return {
        isShow: false,
        query: '',
        currentPage: 0,
      }
    },

    computed: {
      ...mapGetters([
        'searchHistory',
        'playHistory'
      ]),
    },

    created() {
      this.switchTitle = [
        {
          name: '最近播放'
        },
        {
          name: '搜索历史'
        }
      ];
      this.isShowSinger = false;
    },

    methods: {
      //显示与隐藏
      show() {
        this.isShow = true;
        this.refreshList()
      },
      hide() {
        this.isShow = false
      },

      //切换显示列表
      toggle(index) {
        if (!index) {
          this.currentPage = 0
        }
        else {
          this.currentPage = 1
        }
      },

      //搜索
      onQueryChange(query) {
        this.query = query;
        //接收子组件search-box派发的query事件，传入query。
        //进而通过props把query传给子组件suggest
      },
      //发生请求
      addQuery(query) {
        this.$refs.searchBox.setQuery(query)
      },
      //保存搜索记录，同时插入歌曲
      saveHistory(item) {
        this.saveSearchHistory(this.query);
        this.insertSong(item);//添加歌曲
        this.$refs.topTip.show()
      },
      //删除搜索记录
      deleteItem(query) {
        this.deleteSearchHistory(query)
      },

      //失去焦点
      blurInput() {
        //实现收齐键盘
        //直接调用子组件的方法
        this.$refs.searchBox.blur();
      },
      //刷新列表
      refreshList(){
        setTimeout(() => {
          if (this.currentPage === 0) {
            this.$refs.songList.refresh()
          } else {
            this.$refs.searchList.refresh()
          }
        }, 20)
      },

      //从最近列表添加歌曲
      addSong(song, index) {
        this.insertSong(song);
        this.$refs.topTip.show()
      },

      ...mapMutations({
        setCurrentPlay: 'SET_CURRENT_PLAY'
      }),
      ...mapActions([
        'saveSearchHistory',
        'deleteSearchHistory',
        'insertSong'
      ])
    },
    watch: {
      query(newQuery) {
        if (!newQuery || newQuery === '') {
          this.refreshList()
        }
      }
    },
    components: {
      SearchBox,
      SongList,
      SearchList,
      Scroll,
      Switches,
      TopTip,
      Suggest
    }
  }

</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

  .add-song
    position: fixed
    top: 0
    bottom: 0
    width: 100%
    z-index: 200
    background: url("../../common/image/addsong_bg.jpg")
    &.slide-enter-active, &.slide-leave-active
      transition: all 0.3s
    &.slide-enter, &.slide-leave-to
      transform: translate3d(0, 100%, 0)
    .header
      position: relative
      height: 44px
      text-align: center
      .title
        line-height: 44px
        font-size: $font-size-large
        color: $color-text
    .search-box-wrapper
      margin: 20px
    .shortcut
      .list-wrapper
        position: absolute
        top: 165px
        bottom: 50px
        width: 100%
        .list-scroll
          height: 100%
          overflow: hidden
          .list-inner
            padding: 20px 30px
    .search-result
      position: fixed
      top: 124px
      bottom: 0
      width: 100%
    .tip-title
      text-align: center
      padding: 18px 0
      font-size: 0
      .icon-ok
        font-size: $font-size-medium
        color: $color-theme
        margin-right: 4px
      .text
        font-size: $font-size-medium
        color: $color-text
    .close
      position: absolute
      bottom: 0
      width 100%
      text-align: center
      line-height: 50px
      background: $color-opacity
      font-size: $font-size-medium-x
      color: $color-text-l
      .icon-close
        display: block
        padding: 12px
        font-size: 20px
        color: $color-theme

</style>



