<template>
  <div ref="wrapper">
    <slot></slot>
  </div>
</template>

<script type="text/ecmascript-6">
  import BScroll from 'better-scroll'

  export default {
    props: {
      data: {
        type: Array,
        default: null
      },
      probeType: {
        type: Number,
        default: 1
      },
      click: {
        type: Boolean,
        default: false
      },
      isBeforeScroll:{
        type: Boolean,
        default: false
      },
      isListenScroll: {
        type: Boolean,
        default: false
      },
      isPullup:{
        type: Boolean,
        default: false
      },
      isPullDown:{
        type: Boolean,
        default: true
      },
      refreshDelay:{
        type:Number,
        default:20
      }
    },
    mounted() {
      setTimeout(() => {
        this.initBScroll()
      }, 20)
    },
    methods: {
      //初始化scroll
      initBScroll() {
        this.scroll = new BScroll(this.$refs.wrapper, {
          probeType: this.probeType,
          click: this.click,
          PullDownRefresh:this.isPullDown
        });
        if(this.isBeforeScroll){
          let me = this;
          this.scroll.on('beforeScrollStart',()=>{
            me.$emit('beforeScroll')//向父组件派发事件，实现收起键盘
          })
        }
        //监听滚动过程中
        if (this.isListenScroll) {
          let me = this;//me指向scroll.vue实例的Scroll组件
//          this.refresh();
          this.scroll.on('scroll', (position) => {//回调函数中的this是指向BS实例的Scroll，而不是vue实例
            me.$emit('scroll', position)
          })
        }
        //监听滚动结束时
        if(this.isPullup){
          let me = this;
          this.scroll.on('scrollEnd',()=>{
            if(this.scroll.y<=(this.scroll.maxScrollY+50)){//当前滚动距离<最大可滚距离＋50px
              me.$emit('scrollContinue')//派发自定义事件scrollToEnd，传给父组件suggest，实现继续加载条目
            }
          })
        }

      },
      //scroll组件开启
      enable() {
        this.scroll && this.scroll.enable()
      },
      //scroll组件禁用
      disable() {
        this.scroll && this.scroll.disable()
      },
      //scroll组件刷新
      refresh() {
        this.scroll && this.scroll.refresh()
      },
      //scroll组件滚动到"位置"
      scrollTo() {
        this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments)
      },
      //scroll组件滚动到"元素位置"
      scrollToElement() {
        this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments)
      }
    },
    watch: {
      /*
      data是动态渲染的，一开始没有，不需要滚动。
      data渲染完毕，scroll需要滚动。
      所以需要动态监测data的改变，促使其刷新，防止一开始的不要要滚动造成后面也不滚动。
      */
      data(newVal,oldVal) {
        if(newVal===oldVal){
          return
        }
        setTimeout(() => {
          this.refresh()
        }, this.refreshDelay)
      }
    }
  }
</script>



