<template>
  <div class="player" v-show="playList.length>0">
    <!--全屏播放器-->
    <transition name="normal"
                @enter="enter"
                @after-enter="afterEnter"
                @leave="leave"
                @after-leave="afterLeave">
      <div class="normal-player" v-show="fullScreen">
        <!--背景-->
        <div class="background">
          <img width="100%" height="100%" :src="currentSong.image"/>
        </div>
        <!--顶部-->
        <div class="top">
          <div class="back" @click="mini">
            <i class="icon-back"></i>
          </div>
          <h1 class="title">{{currentSong.name}}</h1>
          <h2 class="subtitle">{{currentSong.singer}}</h2>
        </div>
        <!--中间-->
        <div class="middle" ref="middle"
             @touchstart.prevent="startTouchMiddle"
             @touchmove.prevent="moveTouchMiddle"
             @touchend="endTouchMiddle">
          <song-info class="middle-l" :style="plStyle"  ref="songInfo" :playMode="playMode"></song-info>
          <div class="middle-c" :style="cdStyle">
            <div class="cd-wrapper" ref="cdWrapper">
              <div class="cd">
                <img class="image play" :class="playing?'play':'pause'" :src="currentSong.image"/>
              </div>
            </div>
            <div class="playing-lyric-wrapper">
              <div class="playing-lyric">{{playingLyric.current}}</div>
              <div class="playing-lyric">{{playingLyric.next}}</div>
            </div>
          </div>
          <scroll class="middle-r" ref="lyric" :data="currentLyric && currentLyric.lines" :style="lyricStyle">
            <div class="lyric-wrapper" ref="lyricWrapper">
              <div v-if="currentLyric">
                <p v-for="(line,index) in currentLyric.lines"
                   class="text" :class="index===currentLineNumber?'current':''"
                   ref="lyricLine">
                  {{line.txt}}
                </p>
              </div>
            </div>
          </scroll>
        </div>
        <!--底部-->
        <div class="bottom">
          <!--切换cd lyric监听-->
          <div class="dot-wrapper">
            <span class="dot" :class="{'active':currentShow==='list'}"></span>
            <span class="dot" :class="{'active':currentShow==='cd'}"></span>
            <span class="dot" :class="{'active':currentShow==='lyric'}"></span>
          </div>
          <!--操作区域1-->
          <div class="operators">
            <div class="icon i-left" @click="toggleFavorite">
              <i :class="like"></i>
            </div>
            <div class="icon i-center" @click="favorite">
              <i class="download"></i>
            </div>
            <!--<div class="icon i-center" @click="favorite">-->
              <!--<div class="commit"></div>-->
            <!--</div>-->
            <div class="icon i-right" @click="favorite">
              <i class="share"></i>
            </div>
          </div>
          <!--进度-->
          <div class="progress-wrapper">
            <span class="time time-l">{{_format(passTime)}}</span>
            <div class="progress-bar-wrapper">
              <m-progress :percent="passTime/currentSong.duration"
                          @percentChange="onPercentChange"
                          @percentChanging="onPercentChanging"></m-progress>
            </div>
            <span class="time time-r">{{_format(currentSong.duration)}}</span>
          </div>
          <!--操作区域2-->
          <div class="operators">
            <div class="icon i-left" @click="togglePlayMode">
              <i :class="iconMode"></i>
            </div>
            <div class="icon i-left" :class="songReady?'':'disable' " @click="playPrev">
              <i class="icon-prev"></i>
            </div>
            <div class="icon i-center" :class="songReady?'':'disable'" @click="togglePlaying">
              <i :class="playing?'icon-pause':'icon-play'"></i>
            </div>
            <div class="icon i-right" :class="songReady?'':'disable'" @click="playNext">
              <i class="icon-next"></i>
            </div>
            <div class="icon i-right" @click="showPlayList">
              <i class="icon-playlist"></i>
            </div>
          </div>
        </div>
      </div>
    </transition>
    <!--mini播放器-->
    <transition name="mini">
      <div class="mini-player" v-show="!fullScreen" @click="normal()">
        <div class="icon">
          <img width="40" height="40" :class="playing?'play':'pause'" :src="currentSong.image"/>
        </div>
        <div class="text">
          <h2 class="name">{{currentSong.name}}</h2>
          <div class="playing-lyric-wrapper" v-show="playing">
            <div class="playing-lyric" v-html="playingLyric.current"></div>
          </div>
          <p class="desc" v-html="currentSong.singer" v-show="!playing"></p>
        </div>
        <div class="control" @click.stop="togglePlaying">
          <progress-circle :radius="radius" :percent="passTime/currentSong.duration">
            <i :class="playing?'icon-pause-mini':'icon-play-mini'" class="icon-mini"></i>
          </progress-circle>
        </div>
        <div class="control" @click.stop="showPlayList">
          <i class="icon-playlist"></i>
        </div>
      </div>
    </transition>
    <!--播放列表-->
    <playlist ref="playList" :playMode="playMode"></playlist>
    <!--音源-->
    <audio ref="audio"
           :src="currentSong.url"
           @playing="ready"
           @error="error"
           @timeupdate="updateTime"
           @pause="paused"
           @ended="end">
    </audio>
  </div>
</template>

<script>
  import mProgress from '../../base/progress/progress.vue'
  import ProgressCircle from '../../base/progress-circle/progress-circle'
  import scroll from '../../base/scroll/scroll.vue'
  import Playlist from '../../components/playlist/playlist'
  import SongInfo from '../../components/song-info/song-info.vue'
  import {mapMutations, mapGetters, mapActions} from 'vuex'
  import {PLAY_MODE} from '../../store/cofig'
  import {getLyric} from '../../api/song'
  import {ERR_OK} from '../../api/config'
  import {findIndex, shuffle} from '../../common/js/util'
  import animations from 'create-keyframe-animation'
  import Lyric from 'lyric-parser'
  import {Base64} from 'js-base64'


  export default {
    data() {
      return {
        songReady: false,//标志是否请求到播放地址
        passTime: 0,
        radius: 32,
        currentLyric: null,
        currentLineNumber: 0,
        currentShow: 'list',
        plStyle:{
          transform: '',
          transition: 'all 0'
        },
        lyricStyle: {
          transform: '',
          transition: 'all 0'
        },
        cdStyle: {
          opacity: '',
          transition: 'all 0'
        },
        playingLyric: {
          current: '',
          next: ''
        }
      }
    },
    computed: {
      //计算palymode 的class
      iconMode() {
        if (this.playMode === PLAY_MODE.sequence) {
          return 'icon-sequence';
        }

        if (this.playMode === PLAY_MODE.loop) {
          return 'icon-loop';
        }

        if (this.playMode === PLAY_MODE.random) {
          return 'icon-random';
        }

      },

      //计算是否收藏
      like() {
        this.isFavorite=this.favoriteList.findIndex((item)=>{
          return item.id===this.currentSong.id;
        });
        if (this.isFavorite > -1) {
          return 'icon-favorite'
        }
        else {
          return 'icon-not-favorite'
        }
      },

      //mapGetters 辅助函数将 store 中的 getter 映射到局部计算属性，映射 this.playList (相当于一个属性function)为 store.getters.playList。
      //参数是一个Array类型，每一个元素值均来自getters，返回则是映射的getters值,作为一个计算属性存在。
      // 使用对象展开运算符将此对象混入到外部环境中（ computed 属性中）。
      ...mapGetters([
        'playing',
        'playMode',
        'fullScreen',
        'sequenceList',
        'playList',
        'favoriteList',
        'currentIndexPlay',
        'currentSong',
      ]),
    },
    created() {
      this.touch = {}
    },
    methods: {
      //mini，normal的切换
      mini() {
        this.setFullScreen(false)
      },
      normal() {
        this.setFullScreen(true)
      },

      //进入动画
      enter(el, done) {//done是执行enter之后的回调函数，去触发@after-enter事件
        const {translateX, translateY, scale} = this._getPosAndScale();
        //定义miniImage飞到normalImage动画过程
        let animation = {
          0: {
            transform: `translate3d(${translateX}px,${translateY}px,0) scale(${scale})`
          },
          60: {
            transform: `translate3d(0,0,0) scale(1.05)`
          },
          100: {
            transform: `translate3d(0,0,0) scale(1)`
          }
        };
        //注册自定义的animation到animations库
        animations.registerAnimation(
          {
            name: 'move',     //动画名称
            animation,       //自定义的动画过程
            presets: {        //配置
              duration: 500,
              easing: 'linear'
            }
          }
        );
        //执行animation
        animations.runAnimation(
          this.$refs.cdWrapper,//element 谁动
          'move',              //animation 怎么动
          done                 //动完干嘛
        )
      },
      //进入动画完成
      afterEnter() {
        //注销animations库的move动画
        animations.unregisterAnimation('move');
        //从element取消动画
        this.$refs.cdWrapper.style.animation = ``
      },
      //返回动画
      leave(el, done) {
        const {translateX, translateY, scale} = this._getPosAndScale();
        //定义normalImage飞到miniImage动画过程
        let animation = {
          0: {transform: `translate3d(0,0,0) scale(1)`},
          100: {transform: `translate3d(${translateX}px,${translateY}px,0) scale(${scale})`}
        };
        //注册自定义的animation到animations库
        animations.registerAnimation({name: 'return', animation, presets: {duration: 300, easing: 'linear'}});
        //执行animation
        animations.runAnimation(this.$refs.cdWrapper, 'return', done);
      },
      //返回动画结束
      afterLeave() {
        //注销animations库的move动画
        animations.unregisterAnimation('return');
        //从element取消动画
        this.$refs.cdWrapper.style.animation = ``
      },

      //cd 歌词切换
      startTouchMiddle(e) {
        this.touch.initiated = true;//标志位
        this.touch.x = true;//是否横向滚动
        this.touch.startX = e.touches[0].pageX;//第一个触控点x坐标
        this.touch.startY = e.touches[0].pageY//第一个触控点y坐标
      },
      moveTouchMiddle(e) {
        if (!this.touch.initiated) {
          return
        }
        const move = e.touches[0];//滑动时的触控点
        const deltaX = move.pageX - this.touch.startX;//x滑动的距离
        const deltaY = move.pageY - this.touch.startY;//y滑动的距离
        if (Math.abs(deltaY) > Math.abs(deltaX)) {
          this.touch.x = false;
          return;
        }
        let offsetWidth = 0;
        if (this.currentShow === 'list') {//<--最大值是-window.innerWidth，右滑则为0
          offsetWidth = Math.min(0, Math.max(-window.innerWidth, deltaX));//左滑量判断之后取值
          this.touch.percent = Math.abs(offsetWidth / window.innerWidth);//<-滑程度
          this.cdStyle.opacity =1- this.touch.percent;//cd透明度
          this.plStyle.transform = `translate3d(${offsetWidth}px,0,0)`;//lyric平移
        }
        if (this.currentShow === 'cd') {
          offsetWidth = Math.min(0, Math.max(-window.innerWidth, deltaX));//左滑量判断之后取值
          this.touch.percent = Math.abs(offsetWidth / window.innerWidth);
          this.lyricStyle.transform = `translate3d(${offsetWidth}px,0,0)`;//lyric平移
          this.cdStyle.opacity = 1 - this.touch.percent;//cd透明度
        }
        if (this.currentShow === 'lyric') {//右滑最大值是window.innerWidth，左滑则为0
          offsetWidth = Math.min(window.innerWidth, Math.max(0, deltaX));//右滑量判断之后取值
          this.touch.percent = offsetWidth / window.innerWidth;//右滑程度
          this.cdStyle.opacity = this.touch.percent;//cd透明度
          this.lyricStyle.transform = `translate3d(${-window.innerWidth + offsetWidth}px,0,0)`;//lyric平移
        }
      },
      endTouchMiddle(e) {
        if (!this.touch.x) {
          return;
        }
        let offsetWidth = 0;
        //滑动结束时自动滑动到目标位置
        if(this.currentShow === 'list'){
          if (this.touch.percent > 0.2) {//自动到目标位置
            offsetWidth = -window.innerWidth;
            this.currentShow = 'cd';
            this.touch.percent = 1;
          }
          else {
            offsetWidth = 0;
            this.touch.percent = 0;
          }
          this.plStyle.transform = `translate3d(${offsetWidth}px,0,0)`;
          this.cdStyle.opacity = 1 - this.touch.percent;
        }
        else if (this.currentShow === 'cd') {//左滑
          if (this.touch.percent > 0.2) {//自动到目标位置
            offsetWidth = -window.innerWidth;
            this.currentShow = 'lyric';
            this.touch.percent = 1;
          }
          else {
            offsetWidth = 0;
            this.touch.percent = 0;
          }
          this.lyricStyle.transform = `translate3d(${offsetWidth}px,0,0)`;
          this.cdStyle.opacity = 1 - this.touch.percent;
        }
        else if (this.currentShow === 'lyric') {//右滑
          if (this.touch.percent > 0.2) {
            offsetWidth = window.innerWidth;
            this.currentShow = 'cd';
            this.touch.percent = 1
          }
          else {//自动到目标位置
            offsetWidth = 0;
            this.touch.percent = 0
          }
          this.lyricStyle.transform = `translate3d(${-window.innerWidth + offsetWidth}px,0,0)`;
          this.cdStyle.opacity = this.touch.percent;
        }
        this.lyricStyle.transition = `all 0.5s`;
        this.cdStyle.transition = `all 0.5s`;
        this.touch.initiated = false;
      },

      //播放暂停
      togglePlaying() {
        if (!this.songReady) {
          return;
        }
        //用取反可以实现点击则切换播放、暂停。传入false则是暂停，那么还需要在写函数去传入true，逻辑上实现繁琐。
        this.setPlayingState(!this.playing);
        if (this.currentLyric) {
          this.currentLyric.togglePlay()//lyric类方法,实现歌词停止继续跳动
        }
      },
      //上一曲
      playPrev() {
        if (!this.songReady) {//未加载的时候点击无效
          return
        }
        if (this.playList.length <= 1) {//列表只有一首歌曲
          this.playRepeat();
          return;
        }
        this.setCurrentPlay(this.currentIndexPlay - 1);
        if (this.currentIndexPlay < 0) {//第一首歌则调到最后一首歌
          this.setCurrentPlay(this.playList.length - 1)
        }
        if (!this.playing) {   //上一曲自动播放
          this.togglePlaying()
        }
        this.songReady = false;//重置标志位
      },
      //下一曲
      playNext() {//下一曲自动播放
        if (!this.songReady) {//未加载的时候点击无效
          return
        }
        if (this.playList.length <= 1) {//列表只有一首歌曲
          this.playRepeat();
          return;
        }
        this.setCurrentPlay(this.currentIndexPlay + 1);
        if (this.currentIndexPlay >= this.playList.length) {//最后一首歌则跳到第一首歌
          this.setCurrentPlay(0)
        }
        //下一曲自动播放
        if (!this.playing) {
          this.togglePlaying()// 因为audio src加载了 就会默认播放 为了保持一致 需要手动调整play的值
        }
        this.songReady = false;//重置标志位
      },
      //重复本曲
      playRepeat() {
        this.passTime = 0;//设置歌曲走过时间
        this.$refs.audio.play();//播放歌曲
        this.currentLyric.seek();//重置歌词跳动位置
      },
      //切换播放模式(实质上就是改变播放列表
      togglePlayMode() {
        let mode = (this.playMode + 1) % 3;
        this.setPlayMode(mode);//改变播放模式
        let list = [];//根据不同的播放模式设置不同播放列表
        if (mode === PLAY_MODE.random) {
          list = shuffle(this.sequenceList)
        } else {
          list = this.sequenceList;
        }
        this.resetCurrentIndex(list);
        //改变playlist，currentIndex不改变，currentSong必然改变，但是我们期望的是相反的，所以需要重置currentIndex。
        this.setPlayList(list);//改变playlist
      },
      resetCurrentIndex(list) {
        let index = findIndex(list, this.currentSong);//遍历数组找到currentsong的index
        this.setCurrentPlay(index);
      },
      //收藏和取消
      toggleFavorite() {
        if (this.isFavorite < 0) {//this.isFavorite是播放歌曲在喜欢列表里的索引
          this.saveFavoriteList(this.currentSong);
        }
        else {
          this.cancelFavorite(this.currentSong)
        }
      },
      favorite() {

      },
      //显示播放列表
      showPlayList() {
        this.$refs.playList.show();
        this.$refs.songInfo.show();
      },

      //请求声源
      ready() {// 可以确保慢网速或者快速切换歌曲导致的 DOM Exception
        clearTimeout(this.timer);
        this.songReady = true;//请求到歌曲播放的url
        this.canLyricPlay = true;//可以播放歌词（canLyricPlay标志是否可以播放歌词。）
        if (this.currentLyric) {// 如果歌曲的播放晚于歌词的出现，播放的时候需要同步歌词
          this.currentLyric.seek(this.currentTime * 1000)
        }
      },
      error() {
        clearTimeout(this.timer);
        this.songReady = true;//请求不到时，click等都不能执行，为了正常使用重置为true
      },
      //播放时
      updateTime(e) {
        this.passTime = e.target.currentTime;
      },
      //暂停
      paused() {//可能网速或者其他原因导致歌曲暂停，自发性暂停
        /*this.setPlayingState(false);//同步按钮状态
        if (this.currentLyric) {//同步歌词
          this.currentLyric.stop()
        }*/
      },
      //播放完成
      end() {
        if (this.playMode === PLAY_MODE.loop) {
          this.playRepeat();
        }
        else {
          this.playNext();
        }
      },

      //拖动时间条，由子组件派发，执行onPercentChange
      onPercentChanging(percent) {
        this.passTime = this.currentSong.duration * percent;
        if (this.currentLyric) {
          this.currentLyric.seek(this.passTime * 1000)//currentLyric.seek接收ms为单位
        }
      },
      onPercentChange(percent) {
        const curTime = this.currentSong.duration * percent;
        this.passTime = this.$refs.audio.currentTime = curTime;
        if (this.currentLyric) {
          console.log("onPercentChange"+this.passTime);
          this.currentLyric.seek(this.passTime * 1000);
        }
        if (!this.playing) {
          this.togglePlaying()
        }
      },

      //调用api/lyric获取歌词
      receiveLyric() {
        getLyric(this.currentSong.mid).then((res) => {
          if (res.code === ERR_OK) {
            let lyric = Base64.decode(res.lyric);//解码成字符串
            this.playLyric(lyric);
          }
        })
      },
      //播放歌词
      playLyric(lyric) {
        this.currentLyric = new Lyric(lyric, this.handleLyric);//用Lyric库，把lyric解析成对象，函数改变时调用，回调函数赋给this.handleLyric
        if (this.playing && this.canLyricPlay) {
          this.currentLyric.play();//调用Lyric方法，播放歌词
        }
      },
      //歌词滚动和高亮
      handleLyric({lineNum, txt}) {//调用Lyric方法，检测播放歌词的linenumber，并返回lineNum, txt
        console.log("handle");
        if (!this.$refs.lyricLine || !this.canLyricPlay || !this.playing) {
          return
        }
        console.log(lineNum);
        this.currentLineNumber = lineNum;//currentLineNumber->：class->高亮
        this.playingLyric.current = txt;//txt->playingLyric
        if (lineNum > 5) {//如果超过第5行
          const scrollEl = this.$refs.lyricLine[lineNum - 5];//定位到第linenumber-5的p元素（这样让正在播放的歌词在屏幕中间，而不是顶部）
          this.$refs.lyric.scrollToElement(scrollEl, 1000);//滚动到对应元素的位置
        }
        else {
          this.$refs.lyric.scrollTo(0, 0, 1000)//滚动到对应元素的位置
        }
      },

      //获取mini播放器和normal播放器歌手图片的初始position和scale
      _getPosAndScale() {
        const miniImageX = 20 + 40 / 2;
        const miniImageY = window.innerHeight - 10 - 40 / 2;
        const normalImageX = window.innerWidth / 2;
        const normalImageY = normalImageX + 80;
        const scale = 40 / window.innerWidth * 0.8;
        const translateX = miniImageX - normalImageX;
        const translateY = miniImageY - normalImageY;
        return {
          translateX,
          translateY,
          scale
        }
      },
      //时间转换格式
      _format(interval) {
        interval = interval | 0;
        const m = interval / 60 | 0;
        const s = this._pad(interval % 60);
        return `${m}:${s}`
      },
      //补0
      _pad(num, n = 2) {
        let len = num.toString().length;
        while (len < 0) {
          num = '0' + num;
          len++;
        }
        return num;
      },

      //参数是一个obj类型，就是store的mutation，返回则是映射的mutation中的属性function,作为一个method存在。x：y其实是为y起了一个x别名
      // 使用对象展开运算符将此对象混入到外部环境中（ computed 属性中）。
      ...mapMutations({
          setPlayingState: 'SET_PLAYING',
          setPlayMode: 'SET_PLAY_MODE',
          setFullScreen: 'SET_FULL_SCREEN',
          setPlayList: 'SET_PLAYLIST',
          setCurrentPlay: 'SET_CURRENT_PLAY',
        }
      ),
      ...mapActions([
          'savePlayHistory',
          'saveFavoriteList',
          'cancelFavorite'
        ]
      )
    },
    watch: {
      //控制dom播放和获取歌词
      currentSong(newSong, oldSong) {
        if (!newSong.id || !newSong.url || newSong.id === oldSong.id) {
          //歌曲不存在，歌曲url请求失败，歌曲没有变化时返回
          return;
        }
        //恢复初始参数
        this.songReady = false;
        this.canLyricPlay = false;
        if (this.currentLyric) {
          this.currentLyric.stop();//歌曲替换，自动new新的Lyric实例，需要手动删除原来的Lyric实例，解决歌词跳跃
          this.currentLyric = null;
          this.currentTime = 0;
          this.playingLyric = {
            current: '',
            next: ''
          };
          this.currentLineNumber = 0;
        }
        setTimeout(() => {
          this.$refs.audio.play();//播放歌曲
          this.savePlayHistory(newSong);
          this.receiveLyric();//获取歌词
        }, 1000);
      },
      //真正控制dom播放暂停在这里
      playing(newPlaying) {
        if (!this.songReady) {
          return
        }
        const audio = this.$refs.audio;
        this.$nextTick(() => {
          newPlaying ? audio.play() : audio.pause()
        });
      },
    },
    components: {
      mProgress,
      ProgressCircle,
      scroll,
      Playlist,
      SongInfo
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

  .player
    .normal-player
      position: fixed
      left: 0
      right: 0
      top: 0
      bottom: 0
      z-index: 150
      background: $color-player-bg
      .background
        position: absolute
        left: -50%
        top: 0
        width: 200%
        height: 100%
        z-index: -1
        opacity: 0.8
        filter: blur(20px)
      .top
        position: relative
        margin-bottom: 25px
        .back
          position absolute
          top: 0
          left: 6px
          z-index: 50
          .icon-back
            display: block
            padding: 9px
            font-size: $font-size-large-x
            color: $color-theme
            transform: rotate(-90deg)
        .title
          width: 70%
          margin: 0 auto
          line-height: 40px
          text-align: center
          no-wrap()
          font-size: $font-size-large
          color: $color-text
        .subtitle
          line-height: 20px
          text-align: center
          font-size: $font-size-medium
          color: $color-text
      .middle
        position: fixed
        width: 100%
        top: 80px
        bottom: 210px
        white-space: nowrap
        font-size: 0
        .middle-l
          display inline-block
          vertical-align top
          width 100%
          height 100%
          overflow hidden
        .middle-c
          display: inline-block
          vertical-align: top
          position: relative
          width: 100%
          height: 0
          padding-top: 80%
          .cd-wrapper
            position: absolute
            left: 10%
            top: 0
            width: 80%
            height: 100%
            img
              width: 100%
              height: 100%
              box-sizing: border-box
              border: 10px solid rgba(255, 255, 255, 0.1)
              border-radius: 50%
              &.play
                animation: rotate 20s linear infinite
              &.pause
                animation-play-state: paused
              .image
                position: absolute
                left: 0
                top: 0
                width: 100%
                height: 100%
                border-radius: 50%

          .playing-lyric-wrapper
            width: 80%
            /*height 100%*/
            margin: 30px auto 0 auto
            overflow: hidden
            text-align: center
            .playing-lyric
              height: 20px
              line-height: 20px
              font-size: $font-size-medium
              color: $color-text-l
        .middle-r
          display: inline-block
          vertical-align: top
          width: 100%
          height: 100%
          overflow: hidden
          .lyric-wrapper
            width: 80%
            /*height :100%*/
            margin: 0 auto
            overflow: hidden
            text-align: center
            .text
              line-height: 32px
              color: $color-text-l
              font-size: $font-size-medium
              &.current
                color: #FFC107
      .bottom
        position: absolute
        bottom: 35px
        width: 100%
        .dot-wrapper
          text-align: center
          font-size: 0
          padding-bottom 20px
          .dot
            display: inline-block
            vertical-align: middle
            margin: 0 4px
            width: 8px
            height: 8px
            border-radius: 50%
            background: $color-text-l
            &.active
              width: 20px
              border-radius: 5px
              background: $color-text-ll
        .progress-wrapper
          display: flex
          align-items: center
          width: 80%
          margin: 0px auto
          padding: 20px 0 10px 0
          .time
            color: $color-text
            font-size: $font-size-small
            flex: 0 0 30px
            line-height: 30px
            width: 30px
            &.time-l
              text-align: left
            &.time-r
              text-align: right
          .progress-bar-wrapper
            flex: 1
        .operators
          display: flex
          align-items: center
          .icon
            flex: 1
            color: $color-theme
            &.disable
              color: $color-theme-d
            i
              font-size: 30px
          .i-left
            text-align: right
          .i-center
            padding: 0 20px
            text-align: center
            i
              font-size: 40px
          .i-right
            text-align: left
          .icon-favorite
            color: $color-sub-theme
      &.normal-enter-active, &.normal-leave-active
        transition: all 0.4s
        .top, .bottom
          transition: all 0.4s cubic-bezier(0.86, 0.18, 0.82, 1.32)
      &.normal-enter, &.normal-leave-to
        opacity: 0
        .top
          transform: translate3d(0, -100px, 0)
        .bottom
          transform: translate3d(0, 100px, 0)
    .mini-player
      display: flex
      align-items: center
      position: fixed
      left: 0
      bottom: 0
      z-index: 180
      width: 100%
      height: 60px
      background: $color-player-bg
      &.mini-enter-active, &.mini-leave-active
        transition: all 0.4s
      &.mini-enter, &.mini-leave-to
        opacity: 0
      .icon
        flex: 0 0 40px
        width: 40px
        padding: 0 10px 0 20px
        img
          border-radius: 50%
          &.play
            animation: rotate 10s linear infinite
          &.pause
            animation-play-state: paused
      .text
        display: flex
        flex-direction: column
        justify-content: center
        flex: 1
        line-height: 20px
        overflow: hidden
        .name
          margin-bottom: 2px
          no-wrap()
          font-size: $font-size-medium
          color: $color-palyer-title
        .playing-lyric-wrapper
          no-wrap()
          font-size: $font-size-small
          color: white
        .desc
          no-wrap()
          font-size: $font-size-small
          color: $color-text-d
      .control
        flex: 0 0 30px
        width: 30px
        padding: 0 10px
        .icon-play-mini, .icon-pause-mini, .icon-playlist
          font-size: 30px
          color: $color-theme-d
        .icon-mini
          font-size: 32px
          position: absolute
          left: 0
          top: 0

  @keyframes rotate
    0%
      transform: rotate(0)
    100%
      transform: rotate(360deg)
</style>



