import * as types from './mutation-types'
import {PLAY_MODE} from "./cofig"
import {findIndex} from "../common/js/util"


//改变共享数据的动作,obj类型。

const mutations = {

  //每一个动作是一个obj的属性function，参数一state是共享数据，参数二xxx是组件传入的值，赋值操作实现改变数据。
  //对象方法不使用一般不使用箭头函数，key值是不是字符串，所以采用[]的方式[types.xxx]就是["xxx"]相当于xxx

  [types.SET_SINGER](state, singer) {
    state.singer = singer;
  },

  [types.SET_RANK](state, rank) {
    state.rank = rank;
  },

  [types.SET_DISC](state, disc) {
    state.disc = disc;
  },

  [types.SET_SEARCH_HISTORY](state, searchHistory) {
    state.searchHistory = searchHistory;
  },

  //以下与内核播放器相关

  [types.SET_PLAYING](state, playing) {
    state.playing = playing;
  },

  [types.SET_PLAY_MODE](state, playMode) {
    state.playMode = playMode;
  },

  [types.SET_FULL_SCREEN](state, fullScreen) {
    state.fullScreen = fullScreen;
  },

  [types.SET_SEQUENCE_LIST](state, sequenceList) {
    state.sequenceList = sequenceList;
  },

  [types.SET_PLAYLIST](state, playList) {
    state.playList = playList;
  },

  [types.SET_CURRENT_PLAY](state, index) {
    state.currentIndexPlay = index;
  },

  [types.SET_PLAY_HISTORY](state, historyList) {
    state.playHistory = historyList
  },

  [types.SET_FAVORITE_LIST](state,favoriteList){
    state.favoriteList=favoriteList
  }



};

export default mutations;



