<template>
  <div class="song-list">
    <ul>
      <li v-for="(song,index) in songs" class="item" @click="selectSong(song,index)">
        <div class="rank" v-show="isRank">
          <span :class="getRankCla(index)" v-text="getRankText(index)"></span>
        </div>
        <div class="content">
          <h2 class="name">{{song.name}}</h2>
          <p class="desc">{{getDesc(song)}}</p>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    props: {
      songs: {
        type: Array,
        default: []
      },
      isRank:{//是否为排行榜页面的子页面
        type:Boolean,
        default: false
      }
    },
    methods: {
      getDesc(song) {
        return `${song.singer}-${song.album}`
      },
      getRankCla(index){
        //前三名是icon样式   其余是text样式
        if (index <= 2) {
          return `icon icon${index}`
        } else {
          return 'text'
        }
      },
      getRankText(index){
        // 除了前三名 其他都显示文字
        if (index > 2) {
          return index + 1
        }
      },
      //click事件执行selectItem，并且emit事件给父组件music-list,同时携带song和index两个参数
      selectSong(song,index){
        this.$emit('select',song,index)
      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

  .song-list
    .item
      display: flex
      align-items: center
      box-sizing: border-box
      height: 64px
      font-size: $font-size-medium
      .rank
        flex: 0 0 25px
        width: 25px
        margin-right: 30px
        text-align: center
        .icon
          display: inline-block
          width: 25px
          height: 24px
          background-size: 25px 24px
          &.icon0
            bg-image('first')
          &.icon1
            bg-image('second')
          &.icon2
            bg-image('third')

        .text
          color: $color-theme
          font-size: $font-size-large
      .content
        flex: 1
        line-height: 20px
        overflow: hidden
        .name
          no-wrap()
          color: $color-text
        .desc
          no-wrap()
          margin-top: 4px
          color: $color-text-d
</style>



