import storage from 'good-storage'
import {findIndex} from "./util";

//插入和删除操作//maxLen：最大长度//compare:比较函数//val：待存入的值//arr:目标数组
function inserArray(arr, val, compare, maxLen) {
  const index = arr.findIndex(compare);
  if (index === 0) {
    return
  }
  if (index > 0) {
    arr.splice(index, 1)
  }
  arr.unshift(val);
  if (maxLen && arr.length > maxLen) {
    arr.pop()
  }
}

function deleteFromArray(arr, compare) {
  const index = arr.findIndex(compare);
  if (index > -1) {
    arr.splice(index, 1)
  }
}

//搜索历史存储、删除、清空、读取
export function saveSearch(query) {
  //取
  let searchs = storage.get('_search_', []);
  //插入位置
  inserArray(searchs, query, (item) => {
    return item === query
  }, 15);
  //存
  storage.set('_search_', searchs);
  return searchs;
}

export function deleteSearch(query) {
  let searches = storage.get('_search_', []);
  deleteFromArray(searches, (item) => {
    return item === query
  });
  storage.set('_search_', searches);
  return searches
}

export function clearSearch() {
  storage.remove('_search_');
  return []
}

export function loadSearch() {
  return storage.get('_search_', [])
}

//播放历史存储、读取
export function savePlay(song) {
  //取
  let songs = storage.get('_play_', []);
  //插入
  const index = findIndex(songs, song);
  if (index === 0) {
    return
  }
  if (index > 0) {
    songs.splice(index, 1);//原位删除
  }

  songs.unshift(song);//首位增加
  if (songs.length > 50) {
    songs.pop();
  }

  //存
  storage.set('_play_', songs);
  return songs;
}

export function deletePlay(song) {
  let songs = storage.get('_play_', []);
  deleteFromArray(songs, (item) => {
    return item === song
  });
  storage.set('_play_', songs);
  return songs
}

export function clearPlay() {
  storage.remove('_play_');
  return []
}

export function loadPlay() {
  return storage.get('_play_', [])
}

//收藏、取消、读取
export function saveFavorite(song) {
  //取
  let songs = storage.get('_favorite_', []);
  //插入位置
  inserArray(songs, song, (item) => {
    return item === song
  }, 15);
  //存
  storage.set('_favorite_', songs);
  return songs;
}

export function cancleFavorite(song) {
  let favorites = storage.get('_favorite_', []);
  deleteFromArray(favorites, (item) => {
    return item.id === song.id
  });
  storage.set('_favorite_', favorites);
  return favorites
}

export function loadFavorite() {
  return storage.get('_favorite_', [])
}




