import {commonParams, commonOption, debug} from './config'
import {jsonp} from '../common/js/jsonp'
import {getUid} from '../common/js/util'
import axios from 'axios'

export function getLyric(mid) {
  const url = debug ? '/api/lyric' : 'http://www.fairyhan.cn/api/lyric';//与后端约定的路径
  const data = Object.assign({}, commonParams, {
    songmid: mid,
    platform: 'yqq',
    hostUin: 0,
    needNewCode: 0,
    categoryId: 10000000,
    pcachetime: +new Date(),
    format: 'json'
  });
  //axios封装ajax请求 返回promise对象----------- 非跨域请求
  const promiseFromServer = axios.get(
    url,//请求本地server的路径
    {params: data}//传递给server的参数
    )
    .then((res) => {//请求成功执行的逻辑
      return Promise.resolve(res.data)
    });
  return promiseFromServer;
}

export function getVKey(songmid, filename) {
  const url = 'https://c.y.qq.com/base/fcgi-bin/fcg_music_express_mobile3.fcg';//跨域请求的纯地址（不含参数）
  const data = Object.assign({}, commonParams, {//需要传入的参数
    cid: 205361747,
    format: 'json',
    platform: 'yqq',
    hostUin: 0,
    needNewCode: 0,
    uin: 0,
    songmid,
    filename,
    guid: getUid()
  });
  //动态生成script标签请求 返回promise对象-------- jsonp跨域请求
  const promiseFromJsonp = jsonp(url, data, Object.assign({}, commonOption, {
    param: 'callback'
  }));
  return promiseFromJsonp
}



