import {mapGetters} from 'vuex'

export const playListMixin = {
  mounted(){
    this.handlePlayList(this.playList)
  },
  activated(){
    this.handlePlayList(this.playList)
  },
  computed: {
    ...mapGetters([
      'playList'
    ]),
  },
  methods: {
    handlePlayList(playList) {
      throw new Error('请在不同组件中分别实现此函数')
    }
  },
  watch: {
    playList(newVal) {
      this.handlePlayList(newVal)
    }
  }
};



