import originJsonp from 'jsonp'

export function jsonp(murl, data, option) {
  //拼接url:纯净url+参数
  const url = murl + (murl.indexOf('?') < 0 ? '?' : '&') + param(data);

  //跨域请求 crossDomain跨域申请和结果，resolve是结果为成功时执行的逻辑，reject则相反。
  function crossDomain(resolve, reject) {
    //到url所指向的文件,通过option,（传一个参数过去告诉跨域服务端“我想要一段调用option,param函数的js代码，请你返回给我”)
    originJsonp(url, option, (err, data) => {
      //跨域成功
      if (!err) {
        //成功执行resolve，对data处理
        resolve(data);
      }
      //跨域失败
      else {
        console.log("error from jsonp");
        //失败执行reject
        reject(err)
      }
    })
  }

  //用Promise去封装crossDomain，可以使resolve和reject与跨域动作独立，并且可以链式调用的去执行resolve和reject。
  const promise = new Promise(crossDomain);
  return promise;
}

//data拼接成字符串
function param(data) {
  let nurl = '';
  for (var k in data) {
    let value = data[k] !== undefined ? data[k] : '';
    nurl += '&' + k + '=' + encodeURIComponent(value)
  }
  return nurl ? nurl.substring(1) : ''
}



