import { getVKey } from '../../api/song'
import { getUid } from './util'
import { ERR_OK } from '../../api/config'

let urlMap = {};

export class Song {
  //构造函数
  constructor({id, name, mid, singer, album, duration, image}) {
    this.id = id;
    this.name = name;
    this.mid = mid;
    this.singer = singer;
    this.album = album;
    this.duration = duration;
    this.image = image;
    this.filename = `C400${this.mid}.m4a`;
    this.lyric='';
    if (urlMap[this.id]) {// 确保一首歌曲的 id 只对应一个 url
      this.url = urlMap[this.id]
    }
    else {
      this.url='';
      genUrl(this);//获取播放路径
    }
  }
}

export function createSong(musicData) {
  //调用构造函数，传入参数
  return new Song({
    id: musicData.songid,
    name: musicData.songname,
    mid: musicData.songmid,
    singer: filterSinger(musicData.singer),
    album: musicData.albumname,
    duration: musicData.interval,
    image: `//y.gtimg.cn/music/photo_new/T002R300x300M000${musicData.albummid}.jpg?max_age=2592000`,
  })
}

//对歌手名字进行处理，大于一个用‘/’拼接
function filterSinger(singer) {
  let ret = [];
  if (!singer) {
    return ''
  }
  singer.forEach((s) => {
    ret.push(s.name)
  });
  return ret.join('/')
}

//获取播放路径
function genUrl(song) {
  if (song.url) {
    return
  }
  getVKey(song.mid, song.filename)
    .then((res) => {
      if (res.code === ERR_OK) {
        const vkey = res.data.items[0].vkey;
        song.url = `http://dl.stream.qqmusic.qq.com/${song.filename}?vkey=${vkey}&guid=${getUid()}&uin=0&fromtag=66`;
        urlMap[song.id] = song.url
      }
    })
}





