<template>
  <div class="progress-bar" @click="ProgressClick">
    <div ref="barInner" class="bar-inner" @touchstart.prevent="progressTouchStart">
      <div ref="progress" class="progress">
      </div>
      <div class="progress-btn-wrapper">
        <div ref="btn"
             class="progress-btn"
             @touchstart.prevent="progressTouchStart"
             @touchmove.prevent="progressTouchMove"
             @touchend.prevent="progressTouchEnd"></div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        btnTransX: 0,
        proWidth: 0
      }
    },
    created() {
      this.touch = {};//touch三个动作之间方便共享数据
    },
    mounted() {
      //窗口改变时
      window.addEventListener('resize', () => {
        if (this.$refs.barInner.clientWidth) {
          //重置宽度
          this.barWidth = this.$refs.barInner.clientWidth;
        }
      });
    },
    props: {
      percent: {
        type: Number,
        default: 0
      },

    },
    computed: {},
    methods: {
      //touch事件发生执行一串函数
      progressTouchStart(e) {
        this.touch.initiated = true;
        this.touch.startX = e.touches[0].pageX;//第一根手指触碰时的x位置
        this.touch.left = this.$refs.progress.clientWidth//必须先记录下来，不然拖动的时候this.$refs.progress.clientWidth在变化，最后计算出的数值出错
      },
      progressTouchMove(e) {
        if (!this.touch.initiated) {
          return
        }
        let moveX = e.touches[0].pageX;//拖动时的x位置
        let offset = moveX - this.touch.startX + this.touch.left;//moveX - this.touch.startX  是变化的宽度
        this._offset(offset);
        this.$emit('percentChanging', this._getPercent())

      },
      progressTouchEnd(e) {
        this.touch.initiated = false;
        let progress=this.$refs.progress.clientWidth/this.barWidth;
        this.changePercent(progress);
      },
      //click事件
      ProgressClick(e){
        this._offset(e.offsetX);//e.offsetX 点击位置距离元素x距离
        let progress=e.offsetX/this.barWidth;
        this.changePercent(progress);
      },
      //
      changePercent(progress){
        this.$emit("percentChange",progress);//通知player去改变播放进度
      },
      _offset(offset) {
        if (offset > this.$refs.barInner.clientWidth) {//手指向右拖很长，计算出来的长度超过最大值
          offset = this.$refs.barInner.clientWidth
        }
        if (offset < 0) {//手指向左拖很长，计算出来的长度小于最小值
          offset = 0
        }
        this.$refs.btn.style.transform = `translateX(${offset - 8}px)`;//按钮变化
        this.$refs.progress.style.width = offset + "px";//进度条变化
      },
      _getPercent() {
        return this.$refs.progress.clientWidth / this.barWidth
      }
    },
    watch: {
      percent(newPercent) {
        if (newPercent > 0 && !this.touch.initiated) {
          this.barWidth = this.$refs.barInner.clientWidth;
          let offset = newPercent * this.barWidth;
          this._offset(offset);
        }//initiated是一个标志位，放置拖动和watchpercent同时执行_offset
      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .progress-bar
    height: 30px
    .bar-inner
      width 100%
      position: relative
      top: 13px
      height: 4px
      background: rgba(0, 0, 0, 0.3)
      .progress
        position: absolute
        height: 100%
        background: $color-theme
      .progress-btn-wrapper
        position: absolute
        left: -8px
        top: -13px
        width: 30px
        height: 30px
        .progress-btn
          position: relative
          top: 7px
          left: 7px
          box-sizing: border-box
          width: 16px
          height: 16px
          border: 3px solid $color-text
          border-radius: 50%
          background: $color-theme
</style>



