<template>
  <scroll class="suggest"
          ref="suggest"
          :data="result"
          :isPullup="isPullup"
          :isBeforeScroll="isBeforeScroll"
          @beforeScroll="listScroll"
          @scrollContinue="searchMore">
    <ul class="suggest-list">
      <li class="suggest-item" v-for="item in result" @click="selectItem(item)">
        <div class="icon">
          <i :class="_getIconClass(item)"></i>
        </div>
        <div class="name">
          <p class="text" v-html="_getDisplayName(item)"></p>
        </div>
      </li>
      <loading v-show="hasMore"></loading>
    </ul>
    <div class="no-result-wrapper" v-show="!hasMore&&result.length==0">
      <no-result :title="`哭唧唧~炸小鸡没有搜索到~`"></no-result>
    </div>
  </scroll>
</template>

<script>
  import Scroll from '../../base/scroll/scroll.vue'
  import Loading from '../../base/loading/loading.vue'
  import NoResult from '../../base/no-result/no-result.vue'
  import {mapMutations, mapActions} from 'vuex'
  import {search} from '../../api/search'
  import {ERR_OK} from "../../api/config";
  import {filterSinger} from '../../common/js/song'
  import {createSong} from "../../common/js/song";
  import Singer from '../../common/js/singer'
  import {playListMixin} from '../../common/js/mixin'


  export default {
    mixins: [playListMixin],
    props: {
      query: {
        type: String,
        default: ''
      },
      isSearchSinger: {
        type: Boolean,
        default: true
      }
    },

    data() {
      return {
        page: 1,
        isPullup: true,//上拉加载
        hasMore: true,//是否有更多结果条目
        result: []
      }
    },

    created() {
      this.isBeforeScroll = true;//是否监听滚动之前
      this.perpage = 20;
    },

    methods: {
      handlePlayList(playList) {
        const bottom = playList.length > 0 ? '60px' : '';
        this.$refs.suggest.$el.style.bottom = bottom;
        this.$refs.suggest.refresh();
      },
      //滚动之前
      listScroll() {//子组件scroll的触发父组件listScroll方法，
        this.$emit('listScroll')
      },

      //首次加载结果
      startSearch() {
        this.page = 1;
        this.$refs.suggest.scrollTo(0, 0);
        search(this.query, this.page, this.isSearchSinger, this.perpage).then((res) => {
          if (res.code === ERR_OK) {
            this.result = this._genResult(res.data);
            if (this.result.length < this.perpage) {
              this.hasMore = false;
            }
            else {
              this.hasMore = true;
            }
          }
          this._checkMore(res.data.song)
        })

      },
      //继续加载
      searchMore() {//子组件scroll的scrollContinue触发父组件suggestsearchMore方法，实现继续加载
        if (!this.hasMore) {
          return;
        }
        this.page++;
        search(this.query, this.page, this.isSearchSinger, this.perpage).then((res) => {
          if (res.code === ERR_OK) {
            //contact实现追加数组元素，直接赋值则是替换。实现的效果是追加，使得scroll边长，而不是数据替换。
            this.result = this.result.concat(this._genResult(res.data))
          }
          this._checkMore(res.data.song)
        })
      },

      //结果列表中选择一个条目
      selectItem(item) {
        if (item.singerMID) {
          //必须new singer类的 实例，因为vuex数据传唤要接收singer类 类型的参数
          const singer = new Singer({
            id: item.singerMID,
            name: item.singerName
          });
          this.$router.push({
            path: `/search/${singer.id}`
          });
          this.setSinger(singer);
        }
        this.$emit('select',item)
      },

      //根据结果是否存在歌手，设置不同样式,不同显示内容
      _getIconClass(item) {
        if (item.type === 'singer') {
          return 'icon-mine'
        }
        else {
          return 'icon-music'
        }
      },
      _getDisplayName(item) {
        if (item.type === 'singer') {
          return item.singerName
        }
        else {
          //使用song类中的filtersinger把多个歌手连成一个字符串
          return item.name + '-' + item.singer
        }
      },

      //检查是否存在更多结果
      _checkMore(song) {
        //
        if (!song.list.length || (song.curnum + song.curpage * this.perpage) > song.totalnum) {
          this.hasMore = false
        }
      },

      //处理结果和歌曲等等
      _genResult(data) {
        //        根据结果是否存在歌手，进行不同数据处理
        let ret = [];
        if (data.zhida.zhida_singer) {
          //把data.zhida.zhida_singer对象拆分，完全拷贝到ret数组元素中。
          ret.push({...data.zhida.zhida_singer, type: 'singer'})
        }
        if (data.song.list) {
          let list = this._normalizeSongs(data.song.list);
          ret = ret.concat(list)
        }
        return ret;
      },
      _normalizeSongs(list) {
        let ret = [];

        list.forEach((song) => {
          let musicData = {
            songid: song.id,
            songname: song.name,
            songmid: song.mid,
            singer: song.singer,
            albummid: song.album.mid,
            albumname: song.album.name,
            interval: song.interval,
            image: '',
            url: song.id
          };
          ret.push(createSong(musicData))
        });
        return ret
      },

      ...mapMutations({
        // 将 `this.setSinger()` 映射为 `this.$store.commit('SET_SINGER')`
        setSinger: 'SET_SINGER'
      }),
      ...mapActions([
        'insertSong'
      ])
    },

    watch: {
      query() {
        this.startSearch()//请求（搜索框内字段）改变时，开始搜索
      }
    },

    components: {
      Scroll,
      Loading,
      NoResult
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

  .suggest
    height: 100%
    overflow: hidden
    .suggest-list
      padding: 0 30px
      .suggest-item
        display: flex
        align-items: center
        padding-bottom: 20px
      .icon
        flex: 0 0 30px
        width: 30px
        [class^="icon-"]
          font-size: 14px
          color: $color-text-d
      .name
        flex: 1
        font-size: $font-size-medium
        color: $color-text-d
        overflow: hidden
        .text
          no-wrap()
    .no-result-wrapper
      position: absolute
      width: 100%
      top: 50%
      transform: translateY(-50%)
</style>



