<template>
  <transition name="list-fade">
    <div class="playlist" @click="hide" v-show="showPlayList">
      <div class="list-wrapper" @click.stop>  <!--阻止click向下捕获-->
        <!--头部操作区域-->
        <div class="list-header">
          <h1 class="title">
            <i class="icon" :class="_modeIcon" @click="togglePlayMode"></i>
            <span class="text">
              <span>{{_modeText}}</span>
              <span>(  {{playList.length}} )</span>
            </span>
            <span class="clear" @click="showConfirm">
              <i class="icon-clear"></i>
            </span>
          </h1>
        </div>
        <!--列表-->
        <scroll ref="listContent" :data="sequenceList" class="list-content">
          <transition-group ref="list" name="list" tag="ul">
            <li class="item"
                v-for="(song,index) in sequenceList"
                ref="item"
                :key="song.id"
                @click="selectSong(song,index)"
            >
              <i class="current" :class="song.id===currentSong.id ? 'icon-play' : ''"></i>
              <span class="text" v-html="song.name"></span>
              <span @click.stop="toggleFavorite(song)" class="like">
                <i :class="like(song)"></i>
              </span>
              <span @click.stop="deleteOne(song)" class="delete">
                <i class="icon-delete"></i>
              </span>
            </li>
          </transition-group>
        </scroll>
        <!--底部操作区域-->
        <div class="list-operate">
          <div @click="addSong" class="add">
            <i class="icon-add"></i>
            <span class="text">添加歌曲到队列</span>
          </div>
        </div>
      </div>
      <confirm ref="confirm" text="是否清空播放列表" confirmBtnText="清空" @confirm="clearAll"></confirm>
      <add-song ref="addSong"></add-song>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  import Scroll from '../../base/scroll/scroll'
  import Confirm from '../../base/confirm/confirm.vue'
  import AddSong from '../../components/add-song/add-song.vue'
  import {PLAY_MODE} from '../../store/cofig'
  import {mapMutations, mapGetters, mapActions} from 'vuex'
  import {playListMixin} from '../../common/js/mixin'
  import {shuffle, findIndex} from "../../common/js/util";

  export default {
    mixins: [playListMixin],
    data() {
      return {
        showPlayList: false,
        refreshDelay: 120
      }
    },

    computed: {
      //当前列表中播放的歌曲的样式
      _modeIcon() {
        if (this.playMode === PLAY_MODE.sequence) {
          return 'icon-sequence';
        }

        if (this.playMode === PLAY_MODE.loop) {
          return 'icon-loop';
        }

        if (this.playMode === PLAY_MODE.random) {
          return 'icon-random';
        }

      },
      _modeText() {
        return this.playMode === PLAY_MODE.sequence ? '顺序播放' : this.playMode === PLAY_MODE.random ? '随机播放' : '单曲循环'
      },
      //映射
      ...mapGetters([
        'playing',
        'playMode',
        'sequenceList',
        'playList',
        'currentIndexPlay',
        'currentSong',
        'favoriteList'
      ])
    },

    methods: {
      handlePlayList(playList) {

      },
      //播放列表显示隐藏切换
      show() {
        this.showPlayList = true;//显示和隐藏切换时，sroll的dom结构改变
        setTimeout(() => {
          this.$refs.listContent.refresh();//sroll的dom结构改变，所以需要重新计算
          this.scrollToCurrent(this.currentSong);
        }, 20);
      },
      hide() {
        this.showPlayList = false
      },

      //播放模式(实质上就是改变播放列表
      togglePlayMode() {
        let mode = (this.playMode + 1) % 3;
        this.setPlayMode(mode);//改变播放模式
        let list = [];//根据不同的播放模式设置不同播放列表
        if (mode === PLAY_MODE.random) {
          list = shuffle(this.sequenceList)
        } else {
          list = this.sequenceList;
        }
        this.resetCurrentIndex(list);//改变playlist，currentIndex不改变，currentSong必然改变，
        // 但是我们期望的是相反的，所以需要重置currentIndex。
        this.setPlayList(list);//改变playlist
      },
      resetCurrentIndex(list) {
        let index = list.findIndex((item) => {
          return item.id === this.currentSong.id//遍历数组直到找到满足遍历的元素和currentsong是同一首歌的时候返回true，index才能得到此时遍历的索引值
        });
        this.setCurrentPlay(index);
      },

      //选择歌曲并播放
      selectSong(song, index) {
        if (this.playMode === PLAY_MODE.random) {
          const currentIndexPlay = findIndex(this.playList, song);//在《播放列表》中找到《歌曲》的索引值
          this.setCurrentPlay(currentIndexPlay);//当前列表切换歌曲只需要改变CurrentPlay，如果从音乐列表选择歌曲要重置播放列表等
        }
        else {
          this.setCurrentPlay(index);//当前列表切换歌曲只需要改变CurrentPlay，如果从音乐列表选择歌曲要重置播放列表等
        }
        if (!this.playing) {
          this.setPlaying(true);
        }
      },
      //当前列表滚动（保持正在播放歌曲在第一个
      scrollToCurrent(currentSong) {
        const index = findIndex(this.sequenceList, currentSong);//顺序列表中查找播放歌曲的索引
        const scrollEl = this.$refs.item[index];//当前列表定位到第index li元素
        this.$refs.listContent.scrollToElement(scrollEl, 300);//滚动到对应元素的位置
      },

      //向当前列表增加一首歌
      addSong() {
        this.$refs.addSong.show()
      },
      //从当前列表删除一首歌
      deleteOne(song) {
        this.deleteSong(song);
        if (!this.playList.length) {
          this.hide()
        }
      },
      //收藏和取消
      toggleFavorite(song){
        const isFavorite=findIndex(this.favoriteList,song);
        if(isFavorite<0){//this.isFavorite是播放歌曲在喜欢列表里的索引
          this.saveFavoriteList(song);
        }
        else {
          this.cancelFavorite(song)
        }
      },

      //计算是否收藏
      like(song){
        const isFavorite=findIndex(this.favoriteList,song);
        if(isFavorite>-1){
          return 'icon-favorite'
        }
        else {
          return 'icon-not-favorite'
        }
      },

      //清空当前列表
      showConfirm() {//弹窗
        this.$refs.confirm.show();
      },
      clearAll() {//清空
        this.clearSongList();
        this.$refs.confirm.hide();
      },

      ...mapMutations({
        setPlaying: 'SET_PLAYING',
        setPlayMode: 'SET_PLAY_MODE',
        setCurrentPlay: 'SET_CURRENT_PLAY',
        setPlayList: 'SET_PLAYLIST',
      }),
      ...mapActions([
        'selectPlay',
        'deleteSong',
        'clearSongList',
        'saveFavoriteList',
        'cancelFavorite'
      ])
    },

    watch: {
      currentSong(newSong, oldSong) {
        if (!this.showPlayList || newSong.id === oldSong.id) {
          return;
        }
        this.scrollToCurrent(newSong)
      }
    },

    components: {
      Scroll,
      Confirm,
      AddSong
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

  .playlist
    position: fixed
    left: 0
    right: 0
    top: 0
    bottom: 0
    z-index: 200
    background-color: $color-background-d
    &.list-fade-enter-active, &.list-fade-leave-active
      transition: opacity 0.3s
      .list-wrapper
        transition: all 0.3s
    &.list-fade-enter, &.list-fade-leave-to
      opacity: 0
      .list-wrapper
        transform: translate3d(0, 100%, 0)
    &.list-fade-enter
    .list-wrapper
      position: absolute
      left: 0
      bottom: 0
      width: 100%
      background:url("../../common/image/playlist_bg.png")
      .list-header
        position: relative
        padding: 20px 30px 10px 20px
        .title
          display: flex
          align-items: center
          .icon
            margin-right: 10px
            font-size: 30px
            color: $color-theme-d
          .text
            flex: 1
            font-size: $font-size-medium
            color: $color-text-l
          .clear
            extend-click()
            .icon-clear
              font-size: $font-size-medium
              color: $color-text-d
      .list-content
        max-height: 240px
        overflow: hidden
        .item
          display: flex
          align-items: center
          height: 40px
          padding: 0 30px 0 20px
          overflow: hidden
          &.list-enter-active, &.list-leave-active
            transition: all 0.1s
          &.list-enter, &.list-leave-to
            height: 0
          .current
            flex: 0 0 20px
            width: 20px
            font-size: $font-size-small
            color: $color-theme-d
          .text
            flex: 1
            no-wrap()
            font-size: $font-size-medium
            color: $color-text-d
          .like
            extend-click()
            margin-right: 15px
            font-size: $font-size-small
            color: $color-theme
            .icon-favorite
              color: $color-sub-theme
          .delete
            extend-click()
            font-size: $font-size-small
            color: $color-theme
      .list-operate
        width: 140px
        margin: 20px auto 30px auto
        .add
          display: flex
          align-items: center
          padding: 8px 16px
          border: 1px solid $color-text-l
          border-radius: 100px
          color: $color-text-l
          .icon-add
            margin-right: 5px
            font-size: $font-size-small-s
          .text
            font-size: $font-size-small
      .list-close
        text-align: center
        line-height: 50px
        background: $color-background
        font-size: $font-size-medium-x
        color: $color-text-l
</style>



