import Vue from 'vue'
import Router from 'vue-router'

const RecommendPage = () => import('components/recommend-page/recommend-page');
const Disc = () => import('components/disc/disc');
const SingerPage = () => import('components/singer-page/singer-page');
const Singer = () => import('components/singer/singer');
const RankPage = () => import('components/rank-page/rank-page');
const Rank = () => import('components/rank/rank');
const Search = () => import('components/search/search');
const User = () => import('components/user-center/user');

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/recommend-page',
      component: RecommendPage,
      children: [
        {
          path: ':id',
          component: Disc
        }
      ]
    },
    {
      path: '/rank-page',
      component: RankPage,
      children: [
        {
          path: ':id',
          component: Rank
        }
      ]
    },
    {
      path: '/singer-page',
      component: SingerPage,
      children: [
        {
          path: ':id',
          component: Singer
        }
      ]
    },
    {
      path: '/search',
      component: Search,
      children:[
        {
          path:':id',
          component:Singer
        }
      ]
    },
    {
      path: '/user',
      component: User,
    },
    {
      path: '/',
      redirect: '/recommend-page'
    },
  ]
})



