import {loadSearch,loadPlay,loadFavorite} from "../common/js/cache";

//共享数据
const state = {
  singer: {},//歌手

  rank: {},//排行榜单详情

  disc: {},//推荐主题详情

  searchHistory: loadSearch(),//从本地存储读取搜索历史

  //以下与内核播放器相关

  playing: false,//播放状态，暂停？播放？

  playMode: 0,//播放模式

  fullScreen: false,//是否全屏

  sequenceList: [],//顺序列表

  playList: [],//播放列表（顺序列表在播放模式影响下的列表

  currentIndexPlay: -1,//目前处于歌曲的index

  playHistory:loadPlay(),//从本地存储读取最近播放

  favoriteList:loadFavorite()//从本地存储读取收藏歌曲
};

export default state



