import * as types from './mutation-types'
import {PLAY_MODE} from "./cofig";
import {saveSearch, deleteSearch, clearSearch,savePlay,saveFavorite,cancleFavorite} from "../common/js/cache";
import {findIndex, shuffle} from "../common/js/util";

//每一个action是一个function，参数一{state,commit}是一个obj，commit是函数，state是共享数据，参数二是组件传入的值类型是obj，commit操作实现对mutation的进一步封装改变数据。
//mutatation不能异步，所以用commit封装，去提交mutation，进而改变数据
//commit(types.xxx,yyy)进行的mutation就是[types.xxx](state, yyy) {state.xxx = yyy}
export const saveSearchHistory = function ({commit}, query) {
  commit(types.SET_SEARCH_HISTORY, saveSearch(query))
};

export const deleteSearchHistory = function ({commit}, query) {
  commit(types.SET_SEARCH_HISTORY, deleteSearch(query))
};

export const clearSearchHistory = function ({commit}, query) {
  commit(types.SET_SEARCH_HISTORY, clearSearch(query))
};

//以下与内核播放器相关
//选择一首歌曲播放
export const selectPlay = function ({commit, state}, {list, index}) {//当点击一首歌时执行selectPlay 动作，index为顺序列表中索引
  let playList;
  let currentIndexPlay;
  if (state.playMode === PLAY_MODE.random) {
    playList = shuffle(list);//重新洗牌得到《播放列表》
    const playSong = list[index];//定位到选择的《歌曲》
    currentIndexPlay = findIndex(playList, playSong);//在《播放列表》中找到《歌曲》的索引值
  }
  else {
    playList = list;//默认与顺序相同
    currentIndexPlay=index//默认与顺序相同
  }
  commit(types.SET_PLAYING, true);//state.playing=true
  commit(types.SET_FULL_SCREEN, true);//state.全屏=true
  commit(types.SET_SEQUENCE_LIST, list);//state.顺序列表=list
  commit(types.SET_PLAYLIST, playList);//state.播放列表=playlist
  commit(types.SET_CURRENT_PLAY, currentIndexPlay);//state.currentPlayIndex=index
};

//随机播放全部
export const randomPlay = function ({commit, state}, list) {//当点随机播放全部时执行randomPlay 动作
  if(!list.length){
    return
  }
  let playList = shuffle(list);
  commit(types.SET_PLAYING, true);
  commit(types.SET_FULL_SCREEN, true);
  commit(types.SET_PLAY_MODE, PLAY_MODE.random);
  commit(types.SET_SEQUENCE_LIST, list);
  commit(types.SET_PLAYLIST, playList);
  commit(types.SET_CURRENT_PLAY, 0);
};

//删除歌曲
export const deleteSong = function ({commit, state}, song) {//从当前列表删除一首歌
  let plist = state.playList.slice();
  let pindex = findIndex(plist, song);
  plist.splice(pindex, 1);

  let slist = state.sequenceList.slice();
  let sindex = findIndex(slist, song);
  slist.splice(sindex, 1);

  let csong = state.playList[state.currentIndexPlay];
  let currentIndex = state.currentIndexPlay;
  if (pindex < currentIndex) {
    currentIndex--
  }

  commit(types.SET_PLAYLIST, plist);
  commit(types.SET_SEQUENCE_LIST, slist);
  commit(types.SET_CURRENT_PLAY, currentIndex);

  if (!state.playList.length) {
    commit(types.SET_PLAYING, false)
  }
};

//清除播放列表
export const clearSongList = function ({commit, state}) {
  commit(types.SET_PLAYLIST, []);
  commit(types.SET_SEQUENCE_LIST, []);
  commit(types.SET_CURRENT_PLAY, -1);
  commit(types.SET_PLAYING, false);
};

//插入歌曲（到下一首播放的位置）
export const insertSong = function ({commit, state}, song) {
  //处理播放列表
  let plist = state.playList.slice();//记录播放列表
  let pindex = findIndex(plist, song);//索引想要插入的歌曲
  let currentIndex = state.currentIndexPlay;//记录正在播放索引
  if (pindex >= 0) {//想要插入的歌曲在播放列表
    if (pindex > currentIndex) {//在当前播放歌曲的后面
      plist.splice(pindex, 1);//删除
      plist.splice(currentIndex + 1, 0, song)//插入到当前播放的下一位置
    }
    else if (pindex < currentIndex) {//在当前播放歌曲的前面
      plist.splice(pindex, 1);//删除
      currentIndex--;//播放索引-1
      plist.splice(currentIndex + 1, 0, song)//插入
    }
  }
  else {//想要插入的歌曲不在播放列表
    plist.splice(currentIndex + 1, 0, song)//插入
  }

  //处理顺序列表
  let slist = state.sequenceList.slice();
  let sindex = findIndex(slist, song);
  let index = findIndex(state.sequenceList, state.playList[state.currentIndexPlay]);//记录播放歌曲在顺序列表的索引
  if (sindex >= 0) {
    if (sindex > index) {
      slist.splice(sindex, 1);
      slist.splice(index + 1, 0, song)
    }
    else if (sindex < index) {
      slist.splice(sindex, 1);
      index--;//播放索引-1
      slist.splice(index + 1, 0, song)
    }
  }
  else {
    slist.splice(index + 1, 0, song)
  }

  commit(types.SET_PLAYLIST, plist);
  commit(types.SET_SEQUENCE_LIST, slist);
};

//保存播放历史
export const savePlayHistory = function ({commit, state}, song) {
  commit(types.SET_PLAY_HISTORY,savePlay(song));
};

//收藏歌曲，取消收藏歌曲
export const saveFavoriteList = function ({commit, state}, song) {
  commit(types.SET_FAVORITE_LIST,saveFavorite(song))
};
export const cancelFavorite = function ({commit, state}, song) {
  commit(types.SET_FAVORITE_LIST,cancleFavorite(song))
};




