//每一个getter看做一个计算属性或者函数，参数是state，返回值是state.xxx。（与state的属性相对应，命名也相同）

import state from "./state";

export const singer = state => state.singer;

export const rank = state => state.rank;

export const disc = state => state.disc;

export const searchHistory = state => state.searchHistory;

//以下与内核播放器相关

export const playing = state => state.playing;

export const playMode = state => state.playMode;

export const fullScreen = state => state.fullScreen;

export const sequenceList = state => state.sequenceList;

export const playList = state => state.playList;

export const currentIndexPlay = state => state.currentIndexPlay;//播放的歌曲的索引

export const currentSong = state => { //播放的歌曲
  if (!state.playList.length) {
    return ''
  }
  return state.playList[state.currentIndexPlay]
};

export const playHistory = state => state.playHistory;

export const favoriteList=state => state.favoriteList;



